.resume {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.resume h1 {
  font-size: 36px;
  text-align: center;
  margin-bottom: 30px;
}

.resume h2 {
  font-size: 28px;
  margin-bottom: 15px;
  border-bottom: 2px solid #333;
  display: inline-block;
  padding-bottom: 5px;
}

.resume ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.resume li {
  margin-bottom: 10px;
}

.resume .section {
  margin-bottom: 30px;
}

.resume .title {
  font-weight: bold;
}

.resume .subtitle {
  font-weight: bold;
  margin-bottom: 5px;
}

.resume .duration,
.resume .location {
  font-style: italic;
  font-size: 14px;
}

/* Fade-in animation */
.fade-enter {
  opacity: 0;
}

.fade-enter-active {
  opacity: 1;
  transition: opacity 1000ms; /* Adjusted to 1000ms to match appearTimeout in Resume.js */
}
