/* Basic styles for the hero section */
.home {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
}

.hero {
  text-align: center;
}

/* Animation for the title (h1) */
.hero-text-enter {
  opacity: 0;
  transform: translateY(-50px);
}

.hero-text-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 1000ms, transform 1000ms;
}

/* Animation for the description (p) */
.hero-desc-enter {
  opacity: 0;
  transform: translateY(50px);
}

.hero-desc-enter-active {
  opacity: 1;
  transform: translateY(0);
  transition: opacity 1000ms, transform 1000ms;
}